import { Component, h, Element, Prop, State, Listen } from '@stencil/core';

@Component({
  tag: 'ks-order-button-pair',
})
export class OrderButtonPair {

    @Element() root: HTMLElement;

    @State() mobile: boolean = false;

    @Listen('resize', { target: 'window' })
    resizeHandler() {
        this.mobile = window.innerWidth < 640;
    }

    componentDidLoad(){
        this.root.style.display = "block"
        this.resizeHandler();
    }

    @Prop() linkLeft: string = "";
    @Prop() linkRight: string = "";
    @Prop() split: number = 50;


    render() {
        const split = this.mobile ? "100%" : this.split - 1 + "%"

        return(
            <div class="ks-text-decorated uk-text-bold uk-flex uk-flex-wrap uk-margin-top">
                <a href={this.linkLeft} class={`uk-display-block uk-button uk-button-default ${this.mobile ? "uk-margin-small-bottom" : "uk-margin-right"}`}
                    style={{fontSize: "18px", width: split}}
                >
                    <slot name="left" />
                </a>

                <a href={this.linkRight} class="uk-display-block uk-button uk-button-default uk-width-expand"
                    style={{fontSize: "18px"}}
                >
                    <slot name="right" />
                </a>
            </div>
        );
    }
}